import React from 'react';
import { FormControl, FormLabel, RadioGroup, FormControlLabel, Radio } from '@mui/material';
import { COLORS } from '../../helpers/constants';

const RadioButtonGroup = ({ value, onChange,title, options,name ,InputWidth, pleft}) => (
  <FormControl sx={{mt:1,width:InputWidth || '500px',color:COLORS.BLACK}}>
    <FormLabel sx={{color:COLORS.BLACK}} id="demo-radio-buttons-group-label">{title}</FormLabel>
    <RadioGroup
    sx={{flexDirection:'row',pl:pleft || 2,color:COLORS.BLACK}}
      aria-labelledby="demo-radio-buttons-group-label"
      value={value}
      onChange={onChange}
      name={name}
    >
    {options.map((item,index)=><FormControlLabel key={index} value={item.value} control={<Radio color='success'/>} label={item.label} />)}
    </RadioGroup>
  </FormControl>
);

export default RadioButtonGroup;
