import React from "react";
import { SCHEMA_TYPES } from "../../helpers/constants";
import Input from "../input";
import InputDate from "../input/indexDate";
import Select from "../input/Select";
import Counter from "../input/Counter";
import SearchableSelect from "../input/SearchableSelect";
import TimeComponent from "../input/timePicker";
import RadioButtonGroup from "../input/radio";

const SchemaExtractor = ({ schema, onChange, errors, data,handleCounter,multi}) => {
  return schema.map((d) => {
    switch (d.type) {
      case SCHEMA_TYPES.INPUT:
        return (
          <Input
            key={d?.name}
            title={d?.title}
            placeholder={d?.placeholder}
            type={d?.inputType || "text"}
            InputWidth={d?.width} 
            name={d?.name}
            onChange={onChange}
            value={data[d?.name]}
            error={errors.get(d?.name)}
            required={!!d?.validator}
            disabled={d?.disabled}
            adornment={d?.adornment}
          />
        );
      case SCHEMA_TYPES.DATE:
        return (
          <InputDate
            key={d?.name}
            title={d?.title}
            placeholder={d?.placeholder}
            InputWidth={d?.width}
            name={d?.name}
            onChange={onChange}
            value={data[d?.name]}
            error={errors.get(d?.name)}
            required={!!d?.validator}
          />
        );
        case SCHEMA_TYPES.TIME:
        return (
          <TimeComponent
            key={d?.name}
            title={d?.title}
            placeholder={d?.placeholder}
            name={d?.name}
            onChange={onChange}
            value={data[d?.name]}
            error={errors.get(d?.name)}
            required={!!d?.validator}
            InputWidth={d?.width}
          />
        );
      case SCHEMA_TYPES.SELECT:
        return (
          <Select
            key={d?.name}
            title={d?.title}
            placeholder={d?.placeholder}
            name={d?.name}
            InputWidth={d?.width}
            onChange={onChange}
            error={errors.get(d?.name)}
            value={data[d?.name]}
            values={d?.values}
            keyValues={d?.keyValues}
            required={!!d?.validator}
            multi={d?.multi}
            height={d?.height}
          />
        );
      case SCHEMA_TYPES.MASTER:
        return (
          <SearchableSelect
            {...d}
            key={d?.name}
            title={d?.title}
            placeholder={d?.placeholder}
            name={d?.name}
            InputWidth={d?.width}
            onChange={onChange}
            error={errors.get(d?.name)}
            value={data[d?.name]}
            values={d?.values}
            fetch={d?.fetch}
            required={!!d?.validator}
          />
        );
        case SCHEMA_TYPES.COUNTER:
          return(
            <Counter
            key={d?.name}
            title={d?.title}
            // placeholder={d?.placeholder}
            width="50%"
            name={d?.name}
            onChange={onChange}
            value={data[d?.name]}
            error={errors.get(d?.name)}
            required={!!d?.validator}
            handleCounter={handleCounter}
            />
          )
          case SCHEMA_TYPES.RADIO:
          return(
            <RadioButtonGroup
            key={d?.name}
            title={d?.title}
            options={d?.keyValues}
            placeholder={d?.placeholder}
            name={d?.name}
            onChange={onChange}
            value={data[d?.name]}
            error={errors.get(d?.name)}
            required={!!d?.validator}
            InputWidth={d?.width}
            pleft={d?.pl}
            />
          )
      default:
        return null;
    }
  });
};

export default SchemaExtractor;
