import React,{useEffect, useState} from "react";
import styles from "./input.module.scss";
import cx from "classnames";

const Input = ({
  value,
  name,
  placeholder,
  onChange,
  top,
  error,
  height,
  width,
  title,
  classes,
  required,
  icon,
  gray,
  preInput,
  iconLeft,
  disabled,
  onFocus,
  col,
  InputWidth,
  adornment,
  type = "text",
  hideMode
}) => {

  const [emailError, setEmailError] = useState("");
  const [inputType, setInputType] = useState(type);
  const handleWheel = (event) => {
      event.stopPropagation()
      event.preventDefault();
  }

  const handleAdornmentClick = () => {
    if (adornment?.onClick) {
      adornment.onClick();
      setInputType((prevType) => (prevType === "password" ? "text" : "password"));
    }
  };

useEffect(()=>{
  console.log("hideMode---",hideMode)
  if(hideMode===true){
    setInputType('password')
  }else{
    setInputType('text')
  }
},[hideMode])

  return (
    <div
      data-testid="input"
      style={{ marginTop: top,width:'100%',flexDirection:col}}
      className={cx(styles.inputContainer, classes)}
    >
      {!!title && (
        <p className={styles.title}>
          {title}
          {!!required && <span>*</span>}
        </p>
      )}
      <div>
      {!!preInput ? (
        <div style={{ width: !!width && width }} className="flex items-center">
          <div className={cx(styles.preInput, styles.input, "mr-1")}>
            {preInput}
          </div>
          <div style={{ position: "relative" }}>
          <input
            name={name}
            type={!!type ? inputType : undefined}
            style={{ height: !!height && height, width:InputWidth || '500px'}}
            className={cx(
              styles.input,
              { [styles.gray]: gray },
              { [styles.error]: !!error || !!emailError }
            )}
            placeholder={placeholder}
            value={value}
            onChange={onChange}
            disabled={disabled}
            onFocus={onFocus}
            onWheel={handleWheel}
          />
           {adornment && (
          <span
            onClick={handleAdornmentClick}
            style={{
              position: "absolute",
              right: "10px",
              top: "50%",
              transform: "translateY(-50%)",
              cursor: "pointer",
            }}
          >
            {adornment.icon}
          </span>
        )}
        </div>
        </div>
      ) : (
        <div style={{ position: "relative" }}>
        <input
          name={name}
          type={!!type ? inputType : undefined}
          style={{
            height: !!height && height,
            width: InputWidth || '500px',
            paddingLeft: !!iconLeft && "30px",
            paddingRight: (!!icon && !iconLeft) && "28px",
           border:'1px solid black'
          }}
          className={cx(
            styles.input,
            { [styles.gray]: gray },
            { [styles.error]: !!error || !!emailError }
          )}
          placeholder={placeholder}
          value={value}
          onChange={onChange}
          disabled={disabled}
          onFocus={onFocus}
          onWheel={handleWheel}
        />
        {adornment && (
          <span
            onClick={handleAdornmentClick}
            style={{
              position: "absolute",
              right: "10px",
              top: "50%",
              transform: "translateY(-50%)",
              cursor: "pointer",
            }}
          >
            {adornment.icon}
          </span>
        )}
        </div>
      )}
      {(error || emailError) && <p className={styles.error}>{error || emailError}</p>}
      </div>
      <span
       style={{ top: title && "60%" }}
        className={`${
          !!icon
            ? cx(styles.icon, { [styles.left]: iconLeft })
            : "display-none"
        }`}
      >
        {!!icon && icon}
      </span>
    </div>
  );
};

export default Input;