import { Box, Stack, Typography } from "@mui/material";
import React, { useEffect, useState } from "react";
import "./auth.scss";
import Input from "../../components/input";
import { Email, Password, Person } from "@mui/icons-material";
import Button from "../../components/button";
import cx from "classnames";
import { useNavigate } from "react-router-dom";
import useAuth from "../../store/auth";
import Forgot from "./Forgot";
import loginIcon from '../../assets/png/loginLogo.png'
import SmartUdyog from '../../assets/png/dark-logo.png'
import DhootLogo from '../../assets/png/DhootLogoPng.png'
import qr_scan from '../../assets/svg/qr_scan.svg'
import { COLORS } from "../../helpers/constants";
import VisibilityIcon from '@mui/icons-material/Visibility';
import VisibilityOffIcon from '@mui/icons-material/VisibilityOff';
import { useTranslation } from 'react-i18next';


const OperatorLoginPage = () => {
  const { t, i18n } = useTranslation();
  const navigate = useNavigate()
  const [tab, setTab] = useState('Password');
  const [modalOpen, setModalOpen] = useState(false);
  const [error, setError] = useState({password:false,username:false})
  const [data, setData] = useState({
    mode:'yes',
    fullname: "",
    email: "",
    password: "",
    secret_key: "",
    username: "",
    name: ""
  });

  const [passwordMode,setPasswordMode] = useState(true)

  const { login, register } = useAuth();
  useEffect(()=>{
    localStorage.setItem('language','EN')
  },[])
  // const onChange = (e) => {
  //   if (!e.target) return;
  //   setData((prev) => ({ ...prev, [e.target.name]: e.target.value }));
  //   if(data.password!=''){
  //     setError({...error,password:false})
  //   }else{
  //     setError({...error,password:true})
  //   }
  //   if(data.username!=''){
  //     setError({...error,username:false})
  //   }else{
  //     setError({...error,username:true})
  //   }
  // };

  const onChange = (e) => {
    if (!e.target) return;
  
    const { name, value } = e.target;
  
    // Update the data state
    setData((prev) => ({ ...prev, [name]: value }));
  
    // Update the error state based on the current input value
    setError((prevError) => ({
      ...prevError,
      [name]: value.trim() === "" // Check if the field is empty
    }));
  };

  const handleAuth = () => {
    const { email, password, fullname, secret_key, username } = data;
    if(error.username===false && data.username!=''){
      setError({...error,username:false})
      if(error.password===false && data.password!=''){
        login({ username, password });
      }else{
        setError({...error,password:true})
      }
    }else{
      setError({...error,username:true})
    }
   
    // if (tab === 0) {
    // }
    // if (tab === 1) {
    //   register({ email, password, fullname, secret_key, username });
    // }
  };

  return (
    <Box className="auth-layout">
      <Box className="auth-card">
        <div className="login-icon">
          <img
            src={loginIcon}
            alt="auth"
          />
        </div>
        <Stack sx={{ alignItems: 'center' ,overflow:'hidden'}}>
            <div className="company-logo">
            <img src={SmartUdyog} />
            </div>
            <div className="sub-company-logo">
            <img src={DhootLogo} />
            </div>
            <Stack sx={{mt:1, borderRadius: 2, flexDirection: 'row', gap: 1, border: 1, borderColor: COLORS.BG_GRAY, width: 'fit-content', overflow: 'hidden' }}>
              <Stack sx={{ backgroundColor: tab === "Password" ? COLORS.BG_GRAY : '', p: 1, cursor: 'pointer', color: COLORS.TEXT_GRAY }} className="fw-14" onClick={() => setTab("Password")}>Login By Password</Stack>
              <Stack sx={{ p: 1, backgroundColor: tab === "Scanning" ? COLORS.BG_GRAY : '', cursor: 'pointer', color: COLORS.TEXT_GRAY }} className="fw-14" onClick={() => setTab("Scanning")}>Login By Scanning</Stack>
            </Stack>
            <Stack sx={{}}>
              {tab === "Password" ? (
                <Stack sx={{overflow:'hidden'}}>
                  <Typography>{t('username')}</Typography>
                  <Input 
                  // InputWidth={'50%'} 
                  required
                  col={'column'}
                  // title="Username"
                  name="username"
                  onChange={onChange}
                  error={error.username===true?'Please enter correct username':null}
                   />
                  <Typography sx={{mt:1}}>{t('password')}</Typography>
                  <Stack sx={{flexDirection:'row'}}>
                  <Input 
                  // InputWidth={'50%'} 
                  required
                  type={passwordMode?'password':'text'}
                  hideMode={passwordMode}
                  // title="Password"
                  name="password"
                  onChange={onChange}
                  error={error.password===true?'Please enter correct password':null}
                   />
                  {passwordMode===true?(
                    <VisibilityOffIcon fontSize="small" sx={{ml:-4,zIndex:0,alignSelf:'center',cursor:'pointer'}} onClick={()=>{setPasswordMode(false)}} />
                    ):(
                    <VisibilityIcon fontSize="small" sx={{ml:-4,zIndex:0,alignSelf:'center',cursor:'pointer'}} onClick={()=>setPasswordMode(true)} />
                    )}
                   </Stack>
                  <Button classes={'mt-2 rounded'} color={'gray'}
                  // disabled={error.password===true && error.username===true}
                   onClick={handleAuth}
                   >
                    <Typography className="fw-14" color={COLORS.TEXT_GRAY}>
                      {t('login')}
                    </Typography>
                  </Button>
                </Stack>
              ) : (
                <Stack className="qr-scan" sx={{overflow:'hidden'}}>
                  <Typography color={COLORS.LG_GRAY_2} className="fw-14 mt-4">Please scan your QR Code or Barcode ID to Login</Typography>
                  <img  src={qr_scan}/>
                </Stack>
              )}
              <Typography color={COLORS.LG_GRAY_2} className="fw-14 mt-2">For Issue in Login please contact your Supervisor</Typography>
              <Stack sx={{}}>
                <Typography sx={{ textAlign: 'left' ,cursor:'pointer'}} onClick={()=>setModalOpen('forgot')} color={COLORS.TEXT_BLUE} className="fw-12 mt-2">Forgot Password</Typography>
                <Typography sx={{ textAlign: 'left' }} color={COLORS.TEXT_BLUE} className="fw-12 mt-2">Term Condition</Typography>
                <Typography color={COLORS.TEXT_BLUE} className="fw-12 mt-2">Privacy Policy</Typography>
              </Stack>
            </Stack>
        </Stack>
      </Box>
      <Forgot
        open={modalOpen === "forgot"}
        onClose={() =>{ setModalOpen(false)}}
        data={data}
        setData={setData}
        // onSubmit={handleAdd}
      />
    </Box>
  );
};

export default OperatorLoginPage;
