import { configureStore } from "@reduxjs/toolkit";
import authReducer from "./auth/reducer";
// import itemSlice from './materials/itemReducer'
import groupSlice from './group/itemGroup'
// import searchSlice from './storeoutbound/forSearch'
// import orderSlice from './order/itemOrder'
import testSlice from './questionBank/testItem'
import userSlice from './users/user'
import failureSlice from './pending_items/f_items'

export const REDUCER_TYPES = {
  AUTH: "auth",
};

const store = configureStore({
  reducer: {
    [REDUCER_TYPES.AUTH]: authReducer,
    SelectedId:groupSlice,
    USER_ITEM:userSlice,
    Failure_item:failureSlice,
    // InboundItem:itemSlice,
    // SelectSearch:searchSlice,
    // OrderItem:orderSlice,
    TestItem:testSlice
  },
});

export default store;
