import React,{useState} from "react";
import Modal from "../../components/modal";
import Input from "../../components/input";
import { Stack, Typography } from "@mui/material";
import Button from "../../components/button";
import SchemaExtractor from "../../components/UI/SchemaExtractor";
import {SCHEMA_TYPES,VALIDATORS,} from '../../helpers/constants'
import { Validate } from "../../helpers/utils";
import JsonCreator from "../../components/UI/JsonCreator";
import crossIcon from '../../assets/svg/cross-icon.svg'
import useReset from '../../store/forgotPassword'
import { toast } from "react-toastify";
import { Visibility, VisibilityOff } from "@mui/icons-material";

const Forgot = ({ open, onClose, data, setData, onSubmit, loading,edit}) => {
  const {sendOTP,createPassword} = useReset()
  const [step, setStep] = useState(0);
  const [errors, setErrors] = useState(new Map());
  const [makePass,setMakePass] = useState(false)
  const [matchError,setMatchError] = useState('')

  const [showPassword, setShowPassword] = useState(false); 
  const [showConfirmPassword, setShowConfirmPassword] = useState(false);

    
  const handleNext = async () => {
    if (step === 0 ) {
      const _errors = Validate(USER_SCHEMA, data);
      console.log("_errors",_errors)
      setErrors(_errors);
      if (_errors.size) return;
      await send_otp()
      return;
    }
    if (step === 1) {
      const _errors = Validate(PASS_SCHEMA, data);
      setErrors(_errors);
      if (_errors.size) return;
      if(matchError===''){
        await changePassword()
        setStep(0);
      }  
      return;
    } 
  };

  const send_otp =async ()=>{  
    let payloadEmail ={
      "email":data.email 
    }
    let payloadPhone ={
      "phone":data.phone
    }
    const otpRes= await sendOTP(data.mode ==="yes"?payloadEmail:payloadPhone)
    if(otpRes?.success){
      toast.success(otpRes.success)
      setStep((prev) => prev + 1);
    }
  
  }

  const USER_SCHEMA = [
    { 
      // title: "Email or Phone",
      type: SCHEMA_TYPES.RADIO,
      name: "mode",
      validator: VALIDATORS.REQUIRED,
      keyValues: [{"value":"yes","label":"Email"},{"value":"no","label":"Phone"}],
      width:'350px',
      pl:0.2
    },
    data.mode ==="yes" &&({
      title: "Email",
      placeholder: "Enter your Email",
      type: SCHEMA_TYPES.INPUT,
      name: "email",
      validator: VALIDATORS.EMAIL,
    }),
    data.mode ==="no" &&( {
      title: "Phone Number",
      placeholder: "Enter your Phone Number",
      type: SCHEMA_TYPES.INPUT,
      name: "phone",
      validator: VALIDATORS.PHONE,
    })
  ];

  const PASS_SCHEMA = [
    {
      title: "OTP",
      placeholder: "Enter your otp",
      type: SCHEMA_TYPES.INPUT,
      name: "otp",
      inputType:'number',
      validator: VALIDATORS.REQUIRED,
    },
    {
      title: "Password",
      placeholder: "Enter your Password",
      type: SCHEMA_TYPES.INPUT,
      name: "password",
      inputType:"password",
      validator: VALIDATORS.PASSWORD,
      adornment: {
        icon: showPassword ? <VisibilityOff /> : <Visibility />,
        onClick: () => setShowPassword(!showPassword),
      }
      
    },
    {
      title: "Confirm Password",
      placeholder: "Enter Confirm Password",
      type: SCHEMA_TYPES.INPUT,
      inputType:"password",
      name: "confirmPassword",
      validator: VALIDATORS.REQUIRED,
      adornment: {
        icon: showConfirmPassword ? <VisibilityOff /> : <Visibility />,
        onClick: () =>  setShowConfirmPassword(!showConfirmPassword),
      }
    },
   
  ];

  const changePassword =async ()=>{
    let payload ={
      new_password: data.password,
      confirm_password:data.confirmPassword
    }
    let res = await createPassword(data.otp,payload)
    // console.log("forgot createPassword",res)
    if(res?.success){
      toast.success(res.success)
      await onClose()
      setData({})
    }
  }

  const handleChange = (e) => {
    const { name, value } = e.target;
    setErrors((prev) => {
      prev.delete(name);
      return prev;
    });
    if(name==='confirmPassword'){
      console.log("confirmPassword",data.password)
      if(data.password === value){
        setMatchError('')
      }else{
        setMatchError('Password not match')
      }
    }
    setData((prev) => ({ ...prev, [name]: value }));
  };

  const handleBack =async ()=>{
    if(step===1){
      setStep(0)
    }else if(step===0){
      await onClose()
    }
  }

  return (
    <Modal
      open={open}
      onClose={onClose}
      width="min(700px,90vw)"
      hideCloseIcon={crossIcon}
      wrapperClasses="px-6"
    >
      <h3 className="opacity-80">Reset your Password</h3>
      <Stack>
      <SchemaExtractor
          data={data}
          errors={errors}
          onChange={handleChange}
          schema={step===0?USER_SCHEMA:PASS_SCHEMA}
        />
        {matchError &&(<Typography sx={{marginLeft:20,color:'red',fontSize:12,mt:-1}}>{matchError}</Typography>)}
        </Stack>   
      <Stack
        direction="row"
        alignItems="center"
        justifyContent="space-between"
        mt={4}
        mb={1}
      >
        <Button bordered gray onClick={handleBack}>
          Cancel
        </Button>
        {step===1&&(
        <Button color={'light_green'} onClick={()=>handleNext()} loading={loading}>
         <Typography className="fw-20-500-24">Create Password</Typography>
        </Button>
        )}
        {step===0&&(
        <Button color={'light_green'} onClick={()=>handleNext()} loading={loading}>
          <Typography className="fw-20-500-24"> Send OTP</Typography>
        </Button>
        )}
      </Stack>
    </Modal>
  );
};

export default Forgot;
