import React from "react";
import { resolveResponse } from "../api/utils";
import REQUEST, { CONTENT_TYPES } from "../api";
import { ENDPOINTS } from "../api/endpoints";
import { useSelector } from "react-redux";
import { REDUCER_TYPES } from "..";

const useReset = () => {

  const { token } = useSelector((state) => state[REDUCER_TYPES.AUTH]);
  const headers = {
    Authorization: `Bearer ${token}`,
    "Content-Type": CONTENT_TYPES.APPLICATION_JSON,
  };


  const sendOTP = async (payload = {}) => {
    const client = new REQUEST();
    const res = await client.post(
      `auth/request-password-reset/`,
      payload,
      // headers,
      (res) => resolveResponse(res, null)
    );
    return res?.data;
  };

  const createPassword = async (otp,payload = {}) => {
    const client = new REQUEST();
    const res = await client.post(
      `auth/reset-password/${otp}/`,
      payload,
      // headers,
      (res) => resolveResponse(res, null)
    );
    return res?.data;
  };
  const resetPassword = async (payload = {}) => {
    const client = new REQUEST();
    const res = await client.post(
      `auth/change-password/`,
      payload,
      headers,
      (res) => resolveResponse(res, null)
    );
    return res?.data;
  };

 

  return {
    sendOTP,
    createPassword,
    resetPassword
  };
};

export default useReset;
