import React from "react";
import styles from "./input.module.scss";
import cx from "classnames";

const TimeComponent = ({
  value,
  name,
  placeholder,
  onChange,
  top,
  error,
  height,
  width,
  title,
  classes,
  required,
  icon,
  gray,
  preInput,
  iconLeft,
  disabled,
  type,
  onFocus,
  InputWidth
}) => {
  return (
    <div
      data-testid="input"
      style={{ marginTop: top,width:"100%" }}
      className={cx(styles.inputContainer, classes)}
    >
      {!!title && (
        <p className={styles.title}>
          {title}
          {!!required && <span>*</span>}
        </p>
      )}
      <div>
      {!!preInput ? (
        <div>
          <input
            name={name}
            type='time'
            style={{ height: height,width: InputWidth || '500px'}}
            className={cx(
              styles.input,
              { [styles.gray]: gray },
              { [styles.error]: !!error }
            )}
            placeholder={placeholder}
            value={value}
            onChange={onChange}
            disabled={disabled}
            onFocus={onFocus}
          />
        </div>
      ) : (
        <div>
        <input
          name={name}
          type='time'
          style={{
            height: !!height && height,
            width: InputWidth || '500px',
            paddingLeft: !!iconLeft && "30px",
            paddingRight: (!!icon && !iconLeft) && "28px",
           border:'1px solid black'
          }}
          className={cx(
            styles.input,
            { [styles.gray]: gray },
            { [styles.error]: !!error }
          )}
          placeholder={placeholder}
          value={value}
          onChange={onChange}
          disabled={disabled}
          onFocus={onFocus}
        />
        </div>
      )}
      {error && <p className={styles.error}>{error}</p>}
      </div>
      {/* <span
       style={{ top: title && "60%" }}
        className={`${
          !!icon
            ? cx(styles.icon, { [styles.left]: iconLeft })
            : "display-none"
        }`}
      >
        {!!icon && icon}
      </span> */}
    </div>
  );
};

export default TimeComponent;