import React,{useState} from "react";
import Modal from "../../components/modal";
import Input from "../../components/input";
import { Stack } from "@mui/material";
import Button from "../../components/button";
import SchemaExtractor from "../../components/UI/SchemaExtractor";
import {SCHEMA_TYPES,VALIDATORS,} from '../../helpers/constants'
import { Validate } from "../../helpers/utils";
import JsonCreator from "../../components/UI/JsonCreator";

const Forgot = ({ open, onClose, data, setData, onSubmit, loading,edit}) => {
  const USER_SCHEMA = [
    {
      title: "Email or Phone Number",
      placeholder: "Enter Email or Phone Number",
      type: SCHEMA_TYPES.INPUT,
      name: "email",
      validator: VALIDATORS.REQUIRED,
    },
    {
      title: "OTP",
      placeholder: "Enter your otp",
      type: SCHEMA_TYPES.INPUT,
      name: "otp",
      validator: VALIDATORS.REQUIRED,
    }
   
  ];

  const PASS_SCHEMA = [
    {
      title: "Password",
      placeholder: "Enter your Password",
      type: SCHEMA_TYPES.INPUT,
      name: "password",
      validator: VALIDATORS.REQUIRED,
    },
    {
      title: "Confirm Password",
      placeholder: "Enter Confirm Password",
      type: SCHEMA_TYPES.INPUT,
      name: "confirmPassword",
      validator: VALIDATORS.REQUIRED,
    },
   
  ];
  const [errors, setErrors] = useState(new Map());
  const [makePass,setMakePass] = useState(false)

  const handleChange = (e) => {
    const { name, value } = e.target;
    setErrors((prev) => {
      prev.delete(name);
      return prev;
    });
    setData((prev) => ({ ...prev, [name]: value }));
  };
  return (
    <Modal
      open={open}
      onClose={onClose}
      wrapperClasses="px-5 pt-0 pb-4"
      width="min(500px,90vw)"
    >
      <h3 className="opacity-80">Forgot your Password</h3>
      
      <SchemaExtractor
          data={data}
          errors={errors}
          onChange={handleChange}
          schema={makePass?PASS_SCHEMA:USER_SCHEMA}
        />
          
      <Stack
        direction="row"
        alignItems="center"
        justifyContent="space-between"
        mt={4}
        mb={1}
      >
        <Button bordered gray onClick={onClose}>
          Cancel
        </Button>
        {makePass?(
        <Button onClick={()=>setMakePass(true)} loading={loading}>
          Create Password
        </Button>
        ):(
        <Button onClick={()=>setMakePass(true)} loading={loading}>
           Send OTP
        </Button>
        )}
      </Stack>
    </Modal>
  );
};

export default Forgot;
