import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import { REDUCER_TYPES } from "..";
import {
  removeStorage,
  resolveResponse,
  setStorage,
  setToken,
} from "../api/utils";
import { REDUCER } from "./reducer";
import ACTIONS from "./actions";
import { ENDPOINTS } from "../api/endpoints";
import REQUEST, { CONTENT_TYPES } from "../api";

const useAuth = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const state = useSelector((state) => state[REDUCER_TYPES.AUTH]);
  const { token } = useSelector((state) => state[REDUCER_TYPES.AUTH]);
  
  const headers = {
    Authorization: `Bearer ${token}`,
    "Content-Type": CONTENT_TYPES.APPLICATION_JSON,
  };


  //USER LOGIN API
  const login = async (payload = {}) => {
    const onSuccess = (res) =>
      resolveResponse(
        res,
        (data) => {
          // console.log("login res")
          profile(data.token.access)
          setToken(data.token.access);
          // dispatch(
          //   REDUCER[ACTIONS.SET_USER]({
          //     // user: payload,
          //     token: data.token.access,
          //   })
          // );
          
        },
        "Successfully logged In!"
      );

    const client = new REQUEST();
    await client.post(
      `${ENDPOINTS.AUTH}/${ENDPOINTS.LOGIN}/`,
      payload,
      {
        "Content-Type": CONTENT_TYPES.APPLICATION_JSON,
      },
      onSuccess
    );
  };

  // user profile

  const profile = async (tokenn) => {
    const client = new REQUEST();
    const res = await client.get(
      `${ENDPOINTS.ACCESS}/`,
      {},
      {
        Authorization: `Bearer ${tokenn}`,
        "Content-Type": CONTENT_TYPES.APPLICATION_JSON,
      },
      Function.prototype
    );
    // console.log("res access",res.data)
    if(res?.data){
      dispatch(
        REDUCER[ACTIONS.SET_USER]({
          user: res.data ,token:tokenn}))
      setStorage(res.data);
      localStorage.setItem("language",res.data.preferred_language)
      localStorage.setItem("userId",res.data.user_id)
      localStorage.setItem("role",res.data.role)
      localStorage.setItem('draweWidth', 180)
      console.log("Object.keys(res.data.menu_items).length",Object.keys(res.data.menu_items).length)
      
      if(Object.keys(res.data.menu_items).length !== 0){
      localStorage.setItem("menu",JSON.stringify(res.data.menu_items))
      console.log("profile data---",res.data.menu_items)
      setTimeout(() => 
        // navigate("/tenants", { replace: true })
      window.location.replace("/users")
      , 2000);
      }else{
        navigate("/error",{ replace: true })
      }
    }
    // else{
    //   logout()
    // } 
   
    return [];
  };


  //USER REGISTER API
  const register = async (payload = {}) => {
    const onSuccess = (res) =>
      resolveResponse(res, null, "Successfully Registered!");

    const client = new REQUEST();
    const response = await client.post(
      `${ENDPOINTS.AUTH}/${ENDPOINTS.REGISTER}/`,
      payload,
      {
        "Content-Type": CONTENT_TYPES.APPLICATION_JSON,
      },
      onSuccess
    );
    return response;
  };

  //LOGOUT USER
  const logout = () => {
    removeStorage();
    dispatch(REDUCER[ACTIONS.LOGOUT]());
    window.location.replace("/login");

  };

  const getMenu=async (userid)=>{
    const client = new REQUEST();
    const res = await client.get(
      `${ENDPOINTS.AUTH}/menu-item-access/${userid}/`,
      {},
      headers,
      resolveResponse
    );
    if (res?.data) {
      console.log("res  ",res.data)
      return [res?.data];
    }
    return [[], 0];
  };

  const getMachines=async (userid)=>{
    const client = new REQUEST();
    const res = await client.get(
      `/asset/user-asset-map/${userid}/`,
      {},
      headers,
      resolveResponse
    );
    if (res?.data) {
      console.log("res  ",res.data)
      return [res?.data];
    }
    return [[], 0];
  };

    const postMenu = async (userid,payload) => {
      const client = new REQUEST();
      const res = await client.post(
       `${ENDPOINTS.AUTH}/menu-item-access/${userid}/`,
        payload,
        headers,
        (res) => resolveResponse(res, null)
      );
      return res?.data;
    };

    const postMachine = async (userid,payload) => {
      const client = new REQUEST();
      const res = await client.post(
       `asset/user-asset-map/${userid}/`,
        payload,
        headers,
        (res) => resolveResponse(res, null)
      );
      return res?.data;
    };

  return {
    login,
    register,
    logout,
    getMenu,
    postMenu,
    postMachine,
    getMachines
  };
};

export default useAuth;
